<template>
    <div id="heroes">
        <spinner v-show="loading"></spinner>
        <div v-if="showModal" class="modalContainer">
          <div class="modal">
            <div @click="cerrarComic" class="closeModal"><img src="src/assets/btn-close.png" alt=""></div>
            <spinner v-show="loadingComic"></spinner>
            <div class="content">
              <div class="col">
                <div class="image">
                  <img :src="comic[0].images[0].path+'.'+comic[0].images[0].extension" alt="">                
                </div>
              </div>
              <div class="col">
                <h3 class="title">{{comic[0].title}}</h3>
                <p clas="description">{{comic[0].description}}</p>
              </div>
            </div>
            <div class="buttons">
              <button class="btn btn-favoritos"><img src="src/assets/btn-favourites-default.png" alt=""> Add to favourites</button><button class="btn btn-buy"><img src="src/assets/shopping-cart-primary.png" alt=""> Buy for $ {{comic[0].prices[0].price}}</button>
            </div>
          </div>
        </div>
        <div class="main-wrap">
        <div class="col1">
          <div class="titleContainer">
            <img src="src/assets/characters.png" alt="">
            <h2>Characters</h2>
          </div>
          
        <paginate v-if="!loading"
          name="heroesComp"
          :list="heroes"
          :per="10"
          tag="div"
          class="heroesContainer"
        >
                  <div class="hero" v-for="hero in paginated('heroesComp')">
                      <div class="hero-container">
                        <div class="hero-container-description">
                          <h2 class="title">{{hero.name}}</h2>
                          <p class="description">{{hero.description}}</p>
                          <button class="btn-red btn">VIEW MORE</button>
                        </div>
                        <div class="hero-image">
                          <img :src="hero.thumbnail.path+'.'+hero.thumbnail.extension" alt="">                  
                        </div>
                      </div>
                      <div class="comics">
                        <h3>Related comics</h3>
                        <div class="comics-container">
                          <li v-for="comic in hero.comics.items">
                            <a href="" @click="mostrarComic" class="comic" :data-comic="comic.resourceURI" >{{comic.name}}</a>              
                          </li>
                        </div>
                      </div>
                  
                  </div>
                  
                </paginate>
                <paginate-links for="heroesComp" :async="true" ></paginate-links>
                
              </div>
            <div class="col2">
              <div class="favoritos">
                <div class="titleContainer">
                  <img src="src/assets/favourites.png" alt="">                  
                  <h2>My Favourites</h2>
                </div>
                <div class="favoritos-container">
                  <div class="favorito">
                    <div class="thrash">
                      <img src="" alt="">                      
                    </div>
                    <div class="comic-container">
                      <img src="" alt="">
                    </div>
                    <p class="comic-name"></p>
                  </div>
                </div>
              </div>
            </div>
        </div>

    </div>
</template>
<style lang="stylus">
@import "stylus/main.styl"
</style>
<script>
import Spinner from './components/Spinner.vue'
import api from './api'
export default {
  name: 'heroes',
  data () {
    return {
      heroes:[],
      loading:true,
      loadingComic:true,      
      offset:0,
      limit:100,
      paginate: ['heroesComp'],
      comic:'',
      showModal: false
    }
  },
  components:{
    Spinner
  },
  methods:{
    refreshHeroes(){
        const self = this
        this.loading = true
        this.heroes = []
        api.getHeroes(this.limit,this.offset)
          .then(function(heroes){
            self.heroes = heroes
            self.loading = false
          })
    },
    mostrarComic(item){
      item.preventDefault();
      let main = document.getElementsByClassName('main-wrap');
      let header = document.getElementsByClassName('header');
      header[0].classList.add("blur");            
      main[0].classList.add("blur");      
      this.showModal = true;
      const self = this;
      this.comic = [];
      this.loadingComic = true;            
      let itemx = item.currentTarget;
      let comic = itemx.getAttribute("data-comic");
      api.getComic(comic)
          .then(function(returnComic){
            self.comic = returnComic
            self.loadingComic = false
          })
    },
    cerrarComic(item){
      item.preventDefault();
      let main = document.getElementsByClassName('main-wrap');
      let header = document.getElementsByClassName('header');
      header[0].classList.remove("blur");            
      main[0].classList.remove("blur");      
      this.showModal = false;
      this.comic = [];
    },

  },
  watch:{
    selectedCountry:function(){
      this.refreshHeroes();
    }
  },
  mounted:function(){
      this.refreshHeroes();    
  }
}
</script>


